#wpcontent {
    padding: 20px;
}

.uhp-settings-title-container {
    position: relative;
    background: #fff;
    text-align: center;
    border-radius: 20px;
    box-shadow: 0 20px 10px rgba(0,0,0,.1);
    padding: 20px;
    margin-bottom: 20px;

    h1 {
        text-align: center;
        margin-top: 0;
        margin-bottom: 10px;
    }

    .submit-btn {
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 10px;
        color: #FFF;
        background: #000;
        text-transform: uppercase;
        font-weight: 600;
        border: none;
        outline: none;
        box-shadow: none;
        padding: 8px 15px;

        &:hover {
            background: #333;
            cursor: pointer;
        }

        @media screen and (max-width: 782px) {
            position: relative;
            top: auto;
            right: auto;
            margin-top: 20px;
            margin-bottom: 20px;
        }
    }

    .uhp-counter {
        font-size: 16px;
        text-align: center;
    }
}

.uhp-supported-plugins-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 20px;
    margin-bottom: 20px;
    &> div {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 20px;
        background: #fff;
        border-radius: 20px;
        box-shadow: 0 20px 10px rgba(0,0,0,.1);
        position: relative;
        
        &.comming-soon:before {
            background-color: #ededed;
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            opacity: .3;
            border-radius: 20px;
        }

        &.comming-soon:after {
            background-color: #ff6600;
            content: "Coming Soon";
            color: #fff;
            font-size: 12px;
            padding: 5px 10px;
            border-radius: 5px;
            position: absolute;
            top: 10px;
            right: 10px;
        }

        img.logo {
            height: 60px;
        }
        
        hr {
            width: 100%;
        }

        p {
            &.green {
                color: #00b300;
            }

            &.grey {
                color: #999;
            }

            &.orange {
                color: #ff6600;
            }
        }

        .switch {
            position: relative;
            display: inline-block;
            width: 44px;
            height: 24px;
        
            & input[type=checkbox] {
                opacity: 0;
                width: 0;
                height: 0;
        
                &:focus + .slider {
                    box-shadow: 0 0 1px #00b300;
                }
        
                &:checked + .slider {
                    background-color: #00b300;
        
                    &::before {
                        transform: translateX(20px);
                    }
                }

                &:disabled + .slider {
                    opacity: 0.3;
                }
            }
        
            & .slider {
                position: absolute;
                cursor: pointer;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color: #ccc;
                -webkit-transition: .4s;
                transition: .4s;
            
                &:before {
                    position: absolute;
                    content: "";
                    height: 18px;
                    width: 18px;
                    left: 3px;
                    bottom: 3px;
                    background-color: #fff;
                    -webkit-transition: .4s;
                    transition: .4s;
                }
        
                &.round {
                    border-radius: 34px;
        
                    &::before {
                        border-radius: 50%;
                    }
                }
            }
        }
    }
}

.uhp-credits-container {
    margin-top: 60px;
    background: #fff;
    border-radius: 20px;
    box-shadow: 0 20px 10px rgba(0,0,0,.1);
    padding: 20px;
    margin-bottom: 20px;
    text-align: center;
    width: fit-content;
    margin: auto;
    a.buymeacoffee {
        img {
            height: 60px;
            transition: all .3s ease;
        }
        &:hover {
            img {
                transform: scale(1.1);
            }
        }
    }
}